<script>
    export default {
        name: "Profile",
        data(){
            return {
                showText:"",
                isRouter:"",
                userId:"",
                usermsg:{},
            }
        },
        created(){
            this.showText = localStorage.getItem("usermsg");
            this.userId = localStorage.getItem("userId");
            if(this.showText){
                this.isRouter = "/profile/info"
            }else {
                this.isRouter = "/login"
            }
            this.axios.get("/v1/user",{
                params:{userid:this.userId}
            }).then(res=>{
                console.log(res);
                this.usermsg = res;
            }).catch(err=>{});
        },
        methods:{
            goBalance(){
                this.$router.push("/balance")
            },
            goBenefit(){
                this.$router.push("/benefit")
            },
            goPoints(){
                this.$router.push("/points")
            }
        }
    }
</script>

